import { z } from 'zod';

export enum DatasetTypeEnum {
  documents = 'documents',
  metrics = 'metrics',
  // for transai workflows
  events = 'events',
}

export enum FilterOperatorsEnum {
  equals = '=',
  notEquals = '<>',
  greaterThan = '>',
  greaterThanOrEqual = '>=',
  lessThan = '<',
  lessThanOrEqual = '<=',
  in = 'IN',
}

export const FilterValueType = z.union([
  z.string().min(1),
  z.number(),
  z.boolean(),
  z.array(z.union([z.string().min(1), z.number(), z.boolean()])).min(1),
]);
export type FilterValueType = z.infer<typeof FilterValueType>;

export enum FilterGroupTypesEnum {
  and = 'AND',
  or = 'OR',
}

export const FieldValueType = z.union([
  z.string().min(1),
  z.array(z.string().min(1)).min(1),
]);
export type FieldValueType = z.infer<typeof FieldValueType>;

